.buttonn {
    padding: 9px 12px;
    border-radius: 5px;
    background-color: #07393C;
    border: none;
    cursor: pointer;
    color: #FFA400;
    font-weight: 600;
    outline: none;
}

button:disabled {
    background-color: rgba(7, 57, 60, .6);
    color: rgba(255, 164, 0, .6);
    cursor: auto;
}
.form-control {
    padding-top: 2%;
  margin-left: auto;
  margin-right: auto;
  }
  
  .form-control label,
  .form-control input,
  .form-control textarea {
    display: block;
  }
  
  .form-control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .form-control input,
  .form-control textarea {
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem;
  }
  
  .form-control input:focus,
  .form-control textarea:focus {
    outline: none;
    background: #ebebeb;
    border-color: #510077;
  }
  
  .form-control--invalid label,
  .form-control--invalid p {
    color: red;
  }
  
  .form-control--invalid input,
  .form-control--invalid textarea {
    border-color: red;
    background: #ffd1d1;
  }
